import React, { Component } from "react";
import Result from '@/component/result'

export default class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
     user1: props.location.search.replace('?', '').split('&')[0].split('=')[1],
     user2: props.location.search.replace('?', '').split('&')[1].split('=')[1]
    }
  }
  render() {
    const { user1, user2 } = this.state
    return (
      <div className='result-page'>
       <Result user1={user1} user2={user2} props={this.props}/>
      </div>
    )
  }
}