import React, { Component } from 'react';
import request from '@/utils/request'
import { Button, Spin } from 'antd'
import './result.less'
class Result extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      loading: true
    };
  }

  async componentDidMount() {
    const { user1, user2 } = this.props
    let s1 = '', s2 = '', list = []
    await Promise.all([
      request(`https://api.github.com/users/${user1}`).then(res => {
        s1 = res
      }),
      request(`https://api.github.com/users/${user2}`).then(res => {
        s2 = res
      })
    ]);
    if (s1.public_repos > s2.public_repos) {
      s1.result = 'Winner';
      s2.result = 'Loser';
    } else if (s1.public_repos === s2.public_repos) {
      s1.result = 'Draw';
      s2.result = 'Draw';
    } else {
      s1.result = 'Loser';
      s2.result = 'Winner';
    }
    list.unshift(s1, s2)
    this.setState({ data: list, loading: false })
  }

  render() {
    const { data, loading } = this.state;
    return (
      <div className="result-page">
        <Spin spinning={loading} tip="Loading...">
        <div className="result-content"
        >
          {data.length &&
            data.map((item, i) => (
              <div
                key={i}
              >
                <h2>{item.result}</h2>
                <img
                  src={item.avatar_url}
                  alt=""
                />
                <h4>Scores: {item.public_repos}</h4>
                <h4>{item.name == null ? item.login : item.name}</h4>
                <p>{item.location || '--'}
                </p>
                <p>{item.followers}</p>
                <p>{item.following}</p>
                <p>{item.public_repos}
                </p>
              </div>
            ))
          }
        </div>
        <div className='reset-btn'><Button
          type='primary'
          onClick={this.reBack}
        >Reset
        </Button></div>
        </Spin>
      </div>
    );
  }
  reBack = () => {
    sessionStorage.setItem('tabKey', 1)
    const { props } = this.props
    props.history.push({ pathname: '/' })
  }
}

export default Result;
